import { UserInteraction } from "@rp/react/tanstack-api/chat"
import { SummaryState } from "./useChat.types"

type Props = {
  summary?: SummaryState
  userInteraction?: UserInteraction
  engineResponse?: "explainer" | "answer"
}

export const getChatSummaryResponse = ({
  summary,
  userInteraction,
  engineResponse = "explainer",
}: Props) => {
  if (summary) {
    return summary.responseBlock?.explainer ?? summary.response
  }
  if (userInteraction && userInteraction.responseBlock.type === "ENGINE") {
    return userInteraction?.responseBlock[engineResponse] ?? ""
  }
  if (userInteraction && userInteraction.responseBlock.type === "TEXT") {
    return userInteraction?.responseBlock.value ?? ""
  }
  return ""
}
